import React, { useMemo, useEffect, useState, useCallback, useRef } from 'react'
import { Modal, TouchableOpacity, TouchableWithoutFeedback, View, Text, Dimensions, StyleSheet, TextInput } from 'react-native'
import Animated, {
    Extrapolation,
    interpolate,
    useAnimatedScrollHandler,
    useAnimatedStyle,
    useSharedValue,
    withTiming
} from 'react-native-reanimated'
import { Ionicons, Octicons } from '@expo/vector-icons'
import HoverableView from '../HoverableView'
import { normalize } from '../../utils'
import {
    COLORS,
    FONTS,
    FONT_SIZES,
    SPACING,
    SUPPORTED_LANGUAGES,
    DEFAULT_LANGUAGE
} from '../../constants'
import { RangeSlider } from '@react-native-assets/slider'
import { Switch } from 'react-native-paper'

const window = Dimensions.get('window')

const SMOKER_VALUES = ['Non-Smoker', 'Occasionally', 'Regularly'] //nekurak, nepravidelne, pravidelne
const BODY_TYPES = ['Slim', 'Athletic', 'Muscular', 'Curvy']
const PUBIC_HAIR_VALUES = ['Shaved', 'Trimmed', 'Hairy']
const SEXUAL_ORIENTATION = ['Heterosexual', 'Homosexual', 'Bisexual', 'Transsexual']


const DEFAULT_FILTERS = {
    age: [18, 60],
    onlyVerified: false,
    onlyIndependent: false,
    onlyPremium: false
}

const Filters = ({ visible, setVisible, route }) => {
    const params = useMemo(() => ({
        language: SUPPORTED_LANGUAGES.includes(decodeURIComponent(route.params.language)) ? decodeURIComponent(route.params.language) : DEFAULT_LANGUAGE
    }), [route.params])

    const [filters, setFilters] = useState(DEFAULT_FILTERS)
    const [minimumBorderColor, setMinimumBorderColor] = useState(COLORS.placeholder)
    const [maximumBorderColor, setMaximumBorderColor] = useState(COLORS.placeholder)

    const [minAge, setMinAge] = useState(DEFAULT_FILTERS.age[0])
    const [maxAge, setMaxAge] = useState(DEFAULT_FILTERS.age[1])

    useEffect(() => {
        if (visible) {
            translateY.value = withTiming(0, {
                useNativeDriver: true
            })
        } else {
            translateY.value = withTiming(window.height, {
                useNativeDriver: true
            })
        }
    }, [visible])

    useEffect(() => {
        setMinAge(filters.age[0])
        setMaxAge(filters.age[1])
    }, [filters.age])

    const scrollY = useSharedValue(0)
    const scrollHandler = useAnimatedScrollHandler((event) => {
        scrollY.value = event.contentOffset.y
    })

    const translateY = useSharedValue(window.height)

    const modalHeaderTextStyles = useAnimatedStyle(() => {
        return {
            fontFamily: FONTS.medium,
            fontSize: FONT_SIZES.large,
            opacity: interpolate(scrollY.value, [0, 30, 50], [0, 0.8, 1], Extrapolation.CLAMP),
        }
    })

    const modalContainerStyles = useAnimatedStyle(() => {
        return {
            backgroundColor: '#FFF',
            borderRadius: 24,
            width: normalize(750),
            maxWidth: '80%',
            height: normalize(800),
            maxHeight: '80%',
            overflow: 'hidden',
            transform: [{ translateY: translateY.value }]
        }
    })

    const closeModal = () => {
        translateY.value = withTiming(window.height, {
            useNativeDriver: true
        })
        setVisible(false)
    }

    const onMinAgeBlur = useCallback((setBorderColor) => {
        const parsedNumber = Number.parseInt(minAge)
        if (Number.isNaN(parsedNumber) || parsedNumber < 18) {
            setFilters(filters => ({
                ...filters,
                ['age']: [DEFAULT_FILTERS.age[0], filters.age[1]]
            }))
        } else if (parsedNumber >= filters.age[1]) {
            setFilters(filters => ({
                ...filters,
                ['age']: [filters.age[1] - 1, filters.age[1]]
            }))
        } else {
            setFilters(filters => ({
                ...filters,
                ['age']: [parsedNumber, filters.age[1]]
            }))
        }

        setBorderColor(COLORS.placeholder)
    }, [minAge])

    const onMaxAgeBlur = useCallback((setBorderColor) => {
        const parsedNumber = Number.parseInt(maxAge)
        if (Number.isNaN(parsedNumber) || parsedNumber > 60) {
            setFilters(filters => ({
                ...filters,
                ['age']: [filters.age[0], DEFAULT_FILTERS.age[1]]
            }))
        } else if (parsedNumber <= filters.age[0]) {
            setFilters(filters => ({
                ...filters,
                ['age']: [filters.age[0], filters.age[0] + 1]
            }))
        } else {
            setFilters(filters => ({
                ...filters,
                ['age']: [filters.age[0], parsedNumber]
            }))
        }

        setBorderColor(COLORS.placeholder)
    }, [maxAge])

    const onFiltersChange = useCallback((filterName, value) => {
        setFilters(filters => ({
            ...filters,
            [filterName]: value
        }))
    }, [])

    return (
        <Modal transparent={true}
            visible={visible}
            animationType="fade">
            <TouchableOpacity
                style={{ flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: 'rgba(0, 0, 0, 0.5)', cursor: 'default' }}
                activeOpacity={1}
                onPressOut={closeModal}
            >
                <TouchableWithoutFeedback>
                    <Animated.View style={modalContainerStyles}>
                        <View style={styles.modal__header}>
                            <View style={{ flexBasis: 50, flexGrow: 1, flexShrink: 0 }}></View>
                            <View style={{ flexShrink: 1, flexGrow: 0 }}>
                                <Animated.Text style={modalHeaderTextStyles}>Filters</Animated.Text>
                            </View>
                            <View style={{ flexBasis: 50, flexGrow: 1, flexShrink: 0, alignItems: 'flex-end' }}>
                                <HoverableView style={{ marginRight: SPACING.medium, width: SPACING.x_large, height: SPACING.x_large, justifyContent: 'center', alignItems: 'center', borderRadius: 17.5 }} hoveredBackgroundColor={COLORS.hoveredHoveredWhite} backgroundColor={COLORS.hoveredWhite}>
                                    <Ionicons onPress={closeModal} name="close" size={normalize(25)} color="black" />
                                </HoverableView>
                            </View>
                        </View>
                        <Animated.View style={[styles.modal__shadowHeader, modalHeaderTextStyles]} />

                        <Animated.ScrollView scrollEventThrottle={1} onScroll={scrollHandler} style={{ flex: 1, zIndex: 1 }} contentContainerStyle={{ paddingBottom: SPACING.small }}>
                            <View style={[styles.filterSection, { marginTop: SPACING.xxxxx_large - SPACING.small }]}>
                                <Text style={styles.filterHeader}>Age range</Text>

                                <View style={{ flexDirection: 'row', alignItems: 'center', paddingHorizontal: SPACING.small }}>
                                    <Text style={{ fontFamily: FONTS.medium, fontSize: FONT_SIZES.large, marginRight: SPACING.small }}>
                                        18
                                    </Text>
                                    <RangeSlider
                                        style={{ flexGrow: 1 }}
                                        range={filters.age}
                                        value={filters.maxAge}
                                        onValueChange={(value) => onFiltersChange('age', value)}
                                        inboundColor={COLORS.red}
                                        outboundColor="#d3d3d3"
                                        thumbTintColor={COLORS.red}
                                        thumbSize={normalize(20)}
                                        thumbStyle={{
                                            shadowColor: "#000",
                                            shadowOffset: {
                                                width: 0,
                                                height: 2,
                                            },
                                            shadowOpacity: 0.36,
                                            shadowRadius: 4.68,

                                            elevation: 8,
                                        }}
                                        maximumValue={60}
                                        minimumValue={18}
                                        step={1}
                                        animateTransitions
                                    />
                                    <Text style={{ fontFamily: FONTS.medium, fontSize: FONT_SIZES.large, marginLeft: SPACING.small }}>
                                        60+
                                    </Text>
                                </View>
                                <View style={{ flexDirection: 'row', alignItems: 'center', paddingHorizontal: SPACING.small, marginTop: SPACING.small }}>
                                    <View style={{ flexGrow: 1, flexDirection: 'column', borderWidth: 1, borderRadius: 10, borderColor: minimumBorderColor, paddingHorizontal: SPACING.xx_small, paddingVertical: SPACING.xxx_small }}>
                                        <Text style={{ fontFamily: FONTS.light, fontSize: FONT_SIZES.small }}>Minimum</Text>
                                        <TextInput
                                            style={{ fontFamily: FONTS.medium, fontSize: FONT_SIZES.large, outlineStyle: 'none' }}
                                            onChangeText={(value) => setMinAge(value.replace(/[^0-9]/g, ''))}
                                            value={minAge}
                                            onBlur={() => onMinAgeBlur(setMinimumBorderColor)}
                                            onFocus={() => setMinimumBorderColor(COLORS.red)}
                                            keyboardType='numeric'
                                            maxLength={3}
                                        />
                                    </View>
                                    <Text style={{ marginHorizontal: SPACING.medium, fontFamily: FONTS.medium, fontSize: FONTS.h1 }}>
                                        -
                                    </Text>
                                    <View style={{ flexGrow: 1, flexDirection: 'column', borderWidth: 1, borderRadius: 10, borderColor: maximumBorderColor, paddingHorizontal: SPACING.xx_small, paddingVertical: SPACING.xxx_small }}>
                                        <Text style={{ fontFamily: FONTS.light, fontSize: FONT_SIZES.small }}>Maximum</Text>
                                        <TextInput
                                            style={{ fontFamily: FONTS.medium, fontSize: FONT_SIZES.large, outlineStyle: 'none' }}
                                            onChangeText={(value) => setMaxAge(value.replace(/[^0-9]/g, ''))}
                                            value={maxAge === 60 ? maxAge + '+' : maxAge}
                                            onBlur={() => onMaxAgeBlur(setMaximumBorderColor)}
                                            onFocus={() => setMaximumBorderColor(COLORS.red)}
                                            keyboardType='numeric'
                                            maxLength={3}
                                        />
                                    </View>
                                </View>
                            </View>

                            <View style={styles.filterSection}>
                                <Text style={styles.filterHeader}>Services</Text>    
                            </View>

                            <View style={styles.filterSection}>
                                <Text style={styles.filterHeader}>Physical attributes</Text>    
                            </View>

                            <View style={styles.filterSection}>
                                <Text style={styles.filterHeader}>Profile</Text> 
                                
                                <View style={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', marginBottom: SPACING.xx_small }}>
                                    <View style={{ flexDirection: 'column' }}>
                                        <Text style={{ fontFamily: FONTS.medium, fontSize: FONT_SIZES.large }}>
                                            Verified
                                        </Text>
                                        <Text style={{ color: COLORS.grey, fontFamily: FONTS.regular, fontSize: FONT_SIZES.medium, marginTop: 2 }}>
                                            Profiles that underwent identity verification process
                                        </Text>
                                    </View>
                                    <Switch value={filters.onlyVerified}
                                        onValueChange={(value) => onFiltersChange('onlyVerified', value)} color={COLORS.red}
                                    />
                                </View>
                                <View style={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
                                    <View style={{ flexDirection: 'column' }}>
                                        <Text style={{ fontFamily: FONTS.medium, fontSize: FONT_SIZES.large }}>
                                            Independent
                                        </Text>
                                        <Text style={{ color: COLORS.grey, fontFamily: FONTS.regular, fontSize: FONT_SIZES.medium, marginTop: 2 }}>
                                            Not affiliated with agencies
                                        </Text>
                                    </View>
                                    <Switch value={filters.onlyIndependent}
                                        onValueChange={(value) => onFiltersChange('onlyIndependent', value)} color={COLORS.red}
                                    />
                                </View>
                                {//indepent, verified, premium, with reviews ?
                                }
                            </View>
                        </Animated.ScrollView>
                    </Animated.View>
                </TouchableWithoutFeedback>
            </TouchableOpacity>
        </Modal>
    )
}

export default Filters

const styles = StyleSheet.create({
    modal__header: {
        position: 'absolute',
        top: 0,
        right: 0,
        left: 0,
        height: normalize(55),
        //backgroundColor: '#FFF',
        zIndex: 3,
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
    },
    modal__shadowHeader: {
        position: 'absolute',
        top: 0,
        right: 0,
        left: 0,
        height: normalize(55),
        backgroundColor: '#FFF',
        zIndex: 2,
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.15,
        shadowRadius: 5
    },
    filterHeader: {
        fontFamily: FONTS.bold,
        fontSize: FONT_SIZES.x_large,
        marginBottom: SPACING.x_small
    },
    filterSection: {
        marginHorizontal: SPACING.small,
        paddingVertical: SPACING.small,
        borderBottomWidth: 0.5,
        borderColor: COLORS.placeholder
    }
})